
































































































/*   有动态内容的 给style标签价scoped限定后无法覆盖bootstrap样式 */
.cus-detail img{
    width: 100%;
    height: 235px;
}
.cus-detail .avatar{
    width: 21px;
    height: 21px;
}
  /* 文章内容样式 */
.question-title{
    font-size: 18px;
    color: #333;
}
.meta{
    margin-bottom: 20px;
    width: 280px;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}
span.author{
    font-weight: 700;
    color: #444;
}
.bio{
    color: #666;
    font-size: 14px;
}
.cus-text{
    padding: 0;
}
.content p{
    line-height: 1.8;
    margin: 10px 0;
}
.content p a{
    color: #000;
}
.view-more{
    width: 100%;
    height: 40px;
    margin-bottom: 20px;
    line-height: 40px;
    text-align: center;
    background-color: rgba(0, 0, 0, .3);
}
.view-more a{
    color: #666;
}
  /*点赞弹框样式*/
.cus-tip{
    position: fixed;
    bottom: 30%;
    left: 50%;
    width: 100px;
    height: 30px;
    line-height: 30px;
    margin-left: -50px;
    background-color: rgba(0, 0, 0, .6);
    border-radius: 5px;
    text-align: center;
    color: #fff;
    -webkit-transition: all .5s ease;
    transition: all .5s ease;
}
  /* 展示微信二维码 */
.cus-img{
    position: fixed;
    left: 50%;
    top: 50%;
    z-index: 88;
    width: 200px;
    height: 200px;
    margin-left: -100px;
    margin-top: -100px;
    border: 2px solid #999;
    padding: 18px;
    background-color: #fff;
    box-sizing: border-box;
}
.cus-img img{
    width: 160px;
    height: 160px;
}

  /* vue动画效果 */
.show-box-enter-active {
    -webkit-animation: show-box-in .5s;
            animation: show-box-in .5s;
}
.show-box-leave-active {
    -webkit-animation: show-box-out .8s;
            animation: show-box-out .8s;
}
@-webkit-keyframes show-box-in {
0% {
      -webkit-transform: scale(.4) translateX(300px);
              transform: scale(.4) translateX(300px);
      opacity: 1;
}
25% {
      -webkit-transform: scale(.6) translateX(20px);
              transform: scale(.6) translateX(20px);
}
50% {
      -webkit-transform: scale(1) translateX(-20px);
              transform: scale(1) translateX(-20px);
}
75%{
      -webkit-transform: scale(1.3) translateX(10px);
              transform: scale(1.3) translateX(10px);
}
100% {
      -webkit-transform: scale(1);
              transform: scale(1);
}
}
@-webkit-keyframes show-box-out {
0% {
      -webkit-transform: scale(1.3, .7) translateY(-15px);
              transform: scale(1.3, .7) translateY(-15px);
}
30% {
      -webkit-transform: scale(1) translateY(15px);
              transform: scale(1) translateY(15px);
}
60% {
      -webkit-transform: scale(.7, 1.3) translateY(-8px);
              transform: scale(.7, 1.3) translateY(-8px);
}
80%{
      -webkit-transform: scale(1) translateY(8px);
              transform: scale(1) translateY(8px);
}
100% {
      -webkit-transform: scale(1) translateY(100px);
              transform: scale(1) translateY(100px);
}
}
@keyframes show-box-in {
0% {
      -webkit-transform: scale(.4) translateX(300px);
              transform: scale(.4) translateX(300px);
      opacity: 1;
}
25% {
      -webkit-transform: scale(.6) translateX(20px);
              transform: scale(.6) translateX(20px);
}
50% {
      -webkit-transform: scale(1) translateX(-20px);
              transform: scale(1) translateX(-20px);
}
75%{
      -webkit-transform: scale(1.3) translateX(10px);
              transform: scale(1.3) translateX(10px);
}
100% {
      -webkit-transform: scale(1);
              transform: scale(1);
}
}
@keyframes show-box-out {
0% {
      -webkit-transform: scale(1.3, .7) translateY(-10px);
              transform: scale(1.3, .7) translateY(-10px);
}
30% {
      -webkit-transform: scale(1) translateY(20px);
              transform: scale(1) translateY(20px);
}
60% {
      -webkit-transform: scale(.7, 1.3) translateY(-20px);
              transform: scale(.7, 1.3) translateY(-20px);
}
80%{
      -webkit-transform: scale(1) translateY(10px);
              transform: scale(1) translateY(10px);
}
100% {
      -webkit-transform: scale(1) translateY(200px);
              transform: scale(1) translateY(200px);
}
}

  /* 媒体查询 */
@media screen and (min-width: 768px) {
.cus-detail{
      width: 640px;
      margin: 0 auto;
}
.cus-text{
      width: 640px;
}
}



