
.cus-nav{
  padding: 0 20px;
  border-bottom: 0;
  background-color: #00a2ed;
  margin-bottom: 0;
  font-size: 18px;
}
.cus-nav li a:hover,
.cus-nav li a:focus,
.cus-nav li a:visited{
  background-color: transparent;
}
.cus-nav li a{
  padding: 10px;
}
.cus-nav a span{
  font-size: 12px;
  color: #fff;
}
.cus-nav li a.home,
.cus-nav li a.home:hover{
  color: #fff;
}
.cus-nav li a i{
  color: #fff;
}
.cus-ctrl{
  position: absolute;
  top: 5px;
  right: 5px;
  background-color: #fff;
  width: 200px;
}
.cus-ctrl a{
  color: #000;
}
.cus-nav li a i.collect{
  color: yellow;
}
.cus-nav li a i.like{
  color: red;
}

/* vue的过渡动画 */
.rotate-enter-active,
.rotate-leave-active{
  -webkit-transition: all .5s ease;
  transition: all .5s ease;
}
.rotate-enter, .rotate-leave-active {
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
}

/* 夜间模式 */
.night .cus-nav{
  background-color: #222;
}
.night .cus-ctrl{
  background-color: #222;
}
.night .cus-ctrl a{
  color: #fff;
}

  /* 媒体查询 */
@media screen and (min-width: 768px) {
.cus-nav{
    height: 80px;
    padding: 15px 0;
    font-size: 30px;
}
.cus-ctrl{
    width: 320px;
}
}



