
.cus-about[data-v-56723dd0]{
  padding: 30px 0 0 0;
  font-size: 20px;
  color: #3ba776;
  text-align: center;
}
.cus-box[data-v-56723dd0], .cus-box[data-v-56723dd0]::before, .cus-box[data-v-56723dd0]::after{
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.cus-box[data-v-56723dd0]{
  width: 200px;
  height: 200px;
  margin: auto;
  margin-bottom: 30px;
  padding: 40px 0 0 30px;
  color: #222;
  font-size: 24px;
  box-shadow: inset 0 0 0  1px #666;
}

/* 点击下划线效果 */
.cus-box a[data-v-56723dd0]{
  position: relative;
  display: inline-block;
  color: #3ba776;
}
.cus-box a[data-v-56723dd0]::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: #3ba776;
  visibility: hidden;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  -webkit-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
}
.cus-box a[data-v-56723dd0]:hover {
  text-decoration: none;
}
.cus-box a[data-v-56723dd0]:hover::after,
.cus-box a[data-v-56723dd0]:active::after {
  visibility: visible;
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
}
.cus-box i[data-v-56723dd0]{
  width: 30px;
  text-align: center;
}
.cus-box p:nth-of-type(1) i[data-v-56723dd0]{
  color: blue;
}
.cus-box p:nth-of-type(2) i[data-v-56723dd0]{
  color: green;
}
.cus-box p:nth-of-type(3) i[data-v-56723dd0]{
  color: indigo;
}
.cus-box[data-v-56723dd0]::before, .cus-box[data-v-56723dd0]::after{
  content: '';
  z-index: -1;
  margin: -5%;
  box-shadow: inset 0 0 0 2px #3ba776;
  -webkit-animation: clipMe 8s linear infinite;
          animation: clipMe 8s linear infinite;
}
.cus-box[data-v-56723dd0]::before{
  -webkit-animation-delay: -4s;
          animation-delay: -4s;
}
@-webkit-keyframes clipMe{
0%, 100% {
        clip: rect(0px, 220.0px, 2px, 0px);
}
25% {
        clip: rect(0px, 2px, 220.0px, 0px);
}
50% {
        clip: rect(218.0px, 220.0px, 220.0px, 0px);
}
75% {
        clip: rect(0px, 220.0px, 220.0px, 218.0px);
}
}
@keyframes clipMe{
0%, 100% {
        clip: rect(0px, 220.0px, 2px, 0px);
}
25% {
        clip: rect(0px, 2px, 220.0px, 0px);
}
50% {
        clip: rect(218.0px, 220.0px, 220.0px, 0px);
}
75% {
        clip: rect(0px, 220.0px, 220.0px, 218.0px);
}
}



