
.cus-side[data-v-61693d58]{
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10000;
    height: 110%;
    padding: 0;
    overflow-y: auto;
}
.cus-bar[data-v-61693d58]{
    /* 要位移需要宽度 */
    width: 300px;
    height: auto;
    padding: 0;
    background-color: #fff;
    font-size: 18px;
    line-height: 1.42;
    color: #fff;
}
.cus-user[data-v-61693d58]{
    background-color: #00a2ed;
}
.cus-user img[data-v-61693d58]{
    width: 40px;
    height: 40px;
}
.cus-user>div[data-v-61693d58]{
    height: 60px;
    line-height: 50px;
    padding: 10px 20px;
}
.cus-user i[data-v-61693d58]{
    margin-right: 10px;
}
.cus-user a[data-v-61693d58]{
    color: #fff;
    text-decoration: none;
}
.cus-bar h3[data-v-61693d58]{
    color: #00a2ed;
    margin: 0;
    padding: 20px;
    background-color: #f3f3f3;
    font-size: 16px;
}
.cus-bar h3 a[data-v-61693d58]{
    text-decoration: none;
}
.cus-bar h3 i[data-v-61693d58]{
