}

/* vue的过渡动画 */
.show-enter-active[data-v-61693d58],
  .show-leave-active[data-v-61693d58]{
    -webkit-transition: all .3s ease;
    transition: all .3s ease;
}
.show-enter[data-v-61693d58], .show-leave-active[data-v-61693d58] {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
}

  /* 夜间模式 */
.night .cus-user[data-v-61693d58]{
    background-color: #222;
}
.night .cus-user a[data-v-61693d58],
  .night .cus-user span[data-v-61693d58]{
    color: #999;
}
.night .cus-user i[data-v-61693d58]{
    color: #fff;
}
.night .cus-bar h3[data-v-61693d58]{
    background-color: #333;
}
.night .cus-channel[data-v-61693d58],
  .night .cus-item[data-v-61693d58]{
    background-color: #555;
    border: none;
}
.night .cus-channel span[data-v-61693d58]{
    color: #aaa;
    font-weight: 400;
}

 /* 隐藏滚动条 */
[data-v-61693d58]::-webkit-scrollbar{
    background-color: transparent;
}




