
.cus-column[data-v-dba6e4e4]{
  background-color: #eee;
}
.cus-table caption[data-v-dba6e4e4]{
  width: 320px;
  margin: -10px auto 0;
  font-size: 20px;
  color: #41b883;
}
.cus-table td[data-v-dba6e4e4]{
  color: #666;
}
.cus-item[data-v-dba6e4e4]{
  margin-bottom: 10px;
  box-shadow: 0px 3px 8px #999;
}
.cus-item.shake[data-v-dba6e4e4]{
  -webkit-animation: shake 2s ease-in-out infinite;
          animation: shake 2s ease-in-out infinite;
}
.cus-item h3[data-v-dba6e4e4]{
  font-size: 16px;
  color: #666;
}
@-webkit-keyframes shake{
0%{
    -webkit-transform: translateX(-2px);
            transform: translateX(-2px);
}
10%{
    -webkit-transform: translateX(3px);
            transform: translateX(3px);
}
20%{
    -webkit-transform: translateX(-4px);
            transform: translateX(-4px);
}
30%{
    -webkit-transform: translateX(5px);
            transform: translateX(5px);
}
40%{
    -webkit-transform: translateX(-6px);
            transform: translateX(-6px);
}
50%{
    -webkit-transform: translateX(-5px);
            transform: translateX(-5px);
}
60%{
    -webkit-transform: translateX(4px);
            transform: translateX(4px);
}
70%{
    -webkit-transform: translateX(-3px);
            transform: translateX(-3px);
}
80%{
    -webkit-transform: translateX(2px);
            transform: translateX(2px);
}
90%{
    -webkit-transform: translateX(-1px);
            transform: translateX(-1px);
}
100%{
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
}
@keyframes shake{
0%{
    -webkit-transform: translateX(-2px);
            transform: translateX(-2px);
}
10%{
    -webkit-transform: translateX(3px);
            transform: translateX(3px);
}
20%{
    -webkit-transform: translateX(-4px);
            transform: translateX(-4px);
}
30%{
    -webkit-transform: translateX(5px);
            transform: translateX(5px);
}
40%{
    -webkit-transform: translateX(-6px);
            transform: translateX(-6px);
}
50%{
    -webkit-transform: translateX(-5px);
            transform: translateX(-5px);
}
60%{
    -webkit-transform: translateX(4px);
            transform: translateX(4px);
}
70%{
    -webkit-transform: translateX(-3px);
            transform: translateX(-3px);
}
80%{
    -webkit-transform: translateX(2px);
            transform: translateX(2px);
}
90%{
    -webkit-transform: translateX(-1px);
            transform: translateX(-1px);
}
100%{
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
}

/* 媒体查询 */
@media screen and (min-width: 768px) {
.cus-column[data-v-dba6e4e4]{
    width: 640px;
    margin: 0 auto;
}
}



