
*{
  font-family: Microsoft Yahei, Arial;
}
.cus-wrapper{
  width: 100%;
  overflow-x: hidden;
  padding: 0;
  padding-top: 50px;
}
img, a{
  border: none;
}

/* 移动端美化样式 */
input, a, button{
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}
body{
  -webkit-touch-callout:none;
  -webkit-overflow-scrolling: touch;
}
@media screen and (min-width: 768px) {
.cus-wrapper{
    padding-top: 80px;
}
}



