
.cus-box[data-v-c827dc14]{
  position: relative;
  width: 100%;
  overflow-x: hidden;
}
.cus-pic[data-v-c827dc14]{
  width: 500%;
  margin: 0;
  -webkit-animation: carousel 10s linear infinite;
          animation: carousel 10s linear infinite;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}
.cus-pic img[data-v-c827dc14]{
  width: 20%;
  height: 235px;
}
.cus-ctrls[data-v-c827dc14]{
  position: absolute;
  bottom: 10px;
  left: 50%;
  width: 120px;
  padding: 0;
  margin-left: -40px;
}
.cus-ctrls li[data-v-c827dc14]{
  float: left;
  width: 10px;
  height: 10px;
  background-color: #fff;
  margin-right: 5px;
  border-radius: 50%;
  list-style: none;
}
.cus-ctrls li.active[data-v-c827dc14]{
  background-color: #666;
}

/* 轮播图动画 */
@-webkit-keyframes carousel{
0%, 100%{
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
20%{
    -webkit-transform: translateX(-20%);
            transform: translateX(-20%);
}
40%{
    -webkit-transform: translateX(-40%);
            transform: translateX(-40%);
}
60%{
    -webkit-transform: translateX(-60%);
            transform: translateX(-60%);
}
80%{
    -webkit-transform: translateX(-80%);
            transform: translateX(-80%);
}
}
@keyframes carousel{
0%, 100%{
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
20%{
    -webkit-transform: translateX(-20%);
            transform: translateX(-20%);
}
40%{
    -webkit-transform: translateX(-40%);
            transform: translateX(-40%);
}
60%{
    -webkit-transform: translateX(-60%);
            transform: translateX(-60%);
}
80%{
    -webkit-transform: translateX(-80%);
            transform: translateX(-80%);
}
}

  /* 媒体查询 */
@media screen and (min-width: 768px) {
.cus-box[data-v-c827dc14]{
    width: 640px;
    margin: 0 auto;
    overflow: hidden;
}
.cus-pic[data-v-c827dc14]{
    height: auto;
}
}



